.filters {
  width: calc(100% - 25px);

  grid-column: 1;
  grid-row: 2;

  padding-right: 25px;
}

.filters__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
}

.filters__item {
  position: relative;

  display: flex;
  flex-direction: row;
  align-items: center;

  margin-top: 7.5px;
  margin-bottom: 7.5px;
  margin-left: 9px;

  &::before {
    position: absolute;
    left: -9px;
    bottom: -7px;

    content: "";

    width: 100%;
    height: 1px;

    background-color: #C7C7C7;
  }

  &:last-child {
    &::before {
      display: none;
    }
  }
}

.filters__item__icon {
  width: 30px;
  height: 30px;

  margin-right: 10px;
}

.filters__item__name {
  position: relative;
  margin: 0;

  font-family: 'Oswald Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 17px;
  line-height: 25px;
  color: #6D6D6D;

