.main-catalog {
  width: 100%;
  padding-bottom: 147px;
}

.main-catalog__wrapper {
  width: calc(100%);
  max-width: calc(1440px);

  margin-left: auto;
  margin-right: auto;

  display: grid;
  grid-template-columns: 225px 1225px;
  grid-template-rows: 40px auto;
  justify-items: flex-end;
}

.main-catalog__loader-button {
  position: absolute;
  left: 50%;
  bottom: -72px;
  transform: translateX(-50%);

  width: 222px;
  height: 52px;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: #7DDB6E;

  border-radius: 20px;
  border: none;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.25);
  background-color: white;
}
