main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.body--catalog {
  background: #E2E2E2;
}

.half-site--left {
  width: calc(100% - 322px - 30px);
}

.body--catalog main {
  padding-top: 114px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
