.promotion {
  z-index: 1;

  width: calc(100%);
  height: 357px;

  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
  padding-bottom: 10px;
}

.promotion__wrapper {
  max-width: 1440px;
  height: calc(100% - 25px - 25px);

  margin-left: auto;
  margin-right: auto;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-left: 165px;
  padding-right: 100px;
}

.promotion__list {
  list-style: none;
  margin: 0;
  padding: 0;

  height: 100%;

  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-template-rows: 100%;
  gap: 30px;
}

.promotion__item {
  position: relative;

  padding: 30px;

  &:nth-child(1) {
    grid-column: ~"1/3";
    grid-row: 1;
    
    background: linear-gradient(360deg, rgba(82, 255, 0, 0.54) 0%, rgba(255, 184, 0, 0.47) 58.54%);
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 20px;
  }

  &:nth-child(2) {
    grid-column: ~"3/4";
    grid-row: 1;

    background: rgba(235, 126, 126, 0.49);
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 20px;

    & .promotion__item__name {
      font-size: 44px;
      line-height: 54px;
    }
  }

  &:nth-child(3) {
    grid-column: ~"4/5";
    grid-row: 1;

    background: rgba(66, 255, 0, 0.5);
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 20px;

    & .promotion__item__name {
      font-size: 44px;
      line-height: 54px;
    }
  }
}

.promotion__item__name {
  margin: 0;

  font-family: 'MA Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 87px;
  line-height: 106px;
  color: #FFFFFF;

  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(254, 198, 10, 0.25);
}

.promotion__item__button {
  position: absolute;
  right: 10px;
  bottom: 10px;

  width: 69px;
  height: 69px;
  border: 3px solid #f9f9f9;
  box-sizing: border-box;
  background: #fff;
  border-radius: 50%;
}
