.sort {
  height: fit-content;

  grid-column: 2;
  grid-row: 1;
}

.sort__list {
  list-style: none;
  padding: 0;
  margin: 0;

  display: flex;
  flex-direction: row;

  padding-right: 170px;
} 

.sort__item {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin-left: 20px;
  margin-right: 20px;

  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    margin-right: 0;
  }
}

.sort__name {
  margin: 0;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 15px;
  line-height: 18px;

  margin-right: 5px;
}
